.services2{
  padding-bottom: 10px;
  &__items{
    margin-top: 50px;
  }
}

.service-item{
  padding: 17px 20px;
  border-radius: 10px;
  border: 1px solid $primary;
  display: flex;
  align-items: center;
  color: $primary;
  &:hover{
    background-color: $primary;
    color: #fff;
    path{

      stroke: #fff;
    }
  }
  path{
    fill: transparent;
    transition: all .4s;
  }
  svg{
    width: 43px;
    height: auto;
    flex-shrink: 0;
    margin-right: 20px;
  }
  &__text{
    font-size: 22px;
    line-height: 118%;
    font-weight: 700;
  }
}

.service-card{
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  background-color: $primary;
  overflow: hidden;
  color: #FFF;
  font-weight: 700;
  &:hover{
    color: #fff;
    background-color: darken($primary,10%);
  }
  &__img{
    @include imgCover(54.5%);
  }
  &__title{
    padding: 20px;
    flex-grow: 1;
    min-height: 82px;
    @include center;
    text-align: center;
  }
}

.services-info{
  padding-bottom: 50px;
  .text{
    margin-top: 50px;
    img{
      margin-bottom: 50px;
    }
  }
}

.type{
  margin: 25px 0;
  padding: 50px 0;
  background: #ECF9FF;
  &__title{
    max-width: 1120px;
  }
  &__items{
    margin-top: 50px;
  }
}

.type-link{
  padding: 5px 15px;
  @include center;
  text-align: center;
  border-radius: 10px;
  border: 1px solid #E8E8E8;
  background: #FFF;
  min-height: 68px;
  font-size: 16px;
  line-height: 119%;
  font-weight: 600;
  &:hover{
    color: $black;
    border-color: $primary;
  }
}

.what{
  padding:  25px 0;
  overflow: hidden;
  .text,
  .gallery-slider{
    margin-top: 50px;
  }
}

.gallery-slider{
  .swiper{
    &-container{
      width: calc(100% + 30px);
    }
    &-slide{
      width: calc(100% / 3);
      padding-right: 30px;
    }
    &-nav{
      display: flex;
      justify-content: center;
      margin-top: 40px;
    }
    &-button{
      position: static;
      margin: 0 5px;
      img{
        width: 12px;
      }
    }
  }
}

.gallery-item{
  display: block;
  @include imgCover(73.8%);
  border-radius: 10px;
}

.prices{
  padding: 25px 0;
  &__list{
    margin-top: 50px;
    @include grid100gap(30px);
  }
}

.price-item{
  display: flex;
  justify-content: space-between;
  &__title,
  &__num{
    font-size: 16px;
    line-height: 160%;
  }
  &__title{
    font-weight: 500;
    max-width: 370px;
  }
  &__num{
    color: #5C5C5C;
    font-weight: 600;
    white-space: nowrap;
    margin-left: 16px;
  }
}

.service-doctors{
  padding: 24px 0 10px;
  .index-doctors__items{
    margin-top: 50px;
  }
}

.doctor-card__prof{
  font-weight: 400;
}

.about-service{
  padding: 25px 0;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 50px;
  }
  &__period{
    font-size: 22px;
    line-height: 160%;
    font-weight: 700;
    span{
      color: $primary;
    }
  }
  .tab__content{
    margin-top: 50px;
  }
}


@media screen and (max-width: 1200px){
  .about-service{
    &__top{
      flex-direction: column;
      align-items: flex-start;
    }
    .tabs{
      margin-top: 32px;
      order: 2;
    }
  }
}


@media screen and (max-width: 992px){
  .tabs{
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
  }

  .services2{
    padding-bottom: 30px;
    &__items{
      margin-top: 32px;
    }
  }

  .service-card{
    &__title{
      font-size: 18px;
    }
  }

  .services-info{
    padding-bottom: 16px;
    .text{
      margin-top: 32px;
      img{
        margin-bottom: 32px;
      }
    }
  }

  .type{
    margin: 0;
    padding: 16px 0;
    background: transparent;
    &__title{
      font-size: 22px;
    }
    &__items{
      margin-top: 32px;
    }
  }

  .what{
    padding: 16px 0;
    .text,
    .gallery-slider{
      margin-top: 32px;
    }
  }

  .gallery-slider{
    .swiper{
      &-container{
        width: calc(100% + 20px);
      }
      &-slide{
        width: 50%;
        padding-right: 20px;
      }
      &-nav{
        margin-top: 32px;
      }
    }
  }

  .prices{
    padding: 16px 0;
    &__list{
      margin-top: 32px;
      gap: 24px;
    }
  }

  .service-doctors{
    padding: 16px 0 32px;
    .index-doctors__items{
      margin-top: 32px;
    }
  }

  .about-service{
    padding: 16px 0;
    &__top{
      margin-top: 32px;
    }
    .mob-tabs,
    .tab__content{
      margin-top: 32px;
    }
  }
}


@media screen and (max-width: 576px){
  .service-item{
    justify-content: center;
    text-align: center;
    padding: 15px 9px;
    min-height: 68px;
    svg{
      display: none;
    }
    &__text{
      font-size: 16px;
    }
  }

  .type{
    &__items{
      gap: 10px;
    }
  }

  .type-link{
    padding: 6px 10px;
    font-size: 15px;
    line-height: 120%;
    min-height: 48px;
  }

  .gallery-slider{
    .swiper{
      &-container{
        width: 100%;
        overflow: unset;
      }
      &-slide{
        width: 290px;
        max-width: 90%;
      }
    }
  }

  .gallery-item{
    padding-bottom: 100%;
  }

  .price-item{
    &__title{
      max-width: 60%;
    }
  }
}
