@font-face {
  font-family: 'Rubik';
  font-weight: bold;
  font-style: normal;
  src: url("../fonts/Rubik-SemiBold.woff2") format("woff2"), url("../fonts/Rubik-SemiBold.eot") format("embedded-opentype"), url("../fonts/Rubik-SemiBold.woff") format("woff"), url("../fonts/Rubik-SemiBold.ttf") format("truetype");
}

.breadcrumbs{
  @media screen and (min-width: 993px){
    margin: 32px 0;
  }
}

.select{
  border-radius: 10px;
}

body{
  font-family: 'Rubik',sans-serif;
}

.grid3,
.grid4{
  display: grid;
  gap: 30px;
}

.grid3{
  grid-template-columns: repeat(3,calc(100% / 3 - 20px));
}

.grid4{
  grid-template-columns: repeat(4, calc(25% - 90px / 4));
}


@media screen and (max-width: 1200px){
  .grid-laptop3{
    grid-template-columns: repeat(3, calc(100% / 3 - 10px));
    gap: 15px;
  }

  .grid-laptop2{
    grid-template-columns: repeat(2, calc(50% - 7.5px));
    gap: 15px;
  }
}


@media screen and (max-width: 992px){
  .grid-tablet2{
    grid-template-columns: repeat(2, calc(50% - 7.5px));
    gap: 15px;
  }
}


@media screen and (max-width: 576px){
  .grid-mob-full{
    grid-template-columns: 100%;
    gap: 32px;
  }
}
