html{
  scroll-behavior: smooth;
}

.footer{
  &._mt0{
    margin-top: 0;
  }
}

.services-info2{
  padding: 0 0 25px;
  &__grid{
    margin-top: 50px;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 15px));
    gap: 30px;
  }
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: 10px;
    }
  }
}

.heal{
  padding: 50px 0;
  margin: 25px 0;
  background: #ECF9FF;
  &:last-child{
    margin-bottom: 0;
  }
  .alphabet-items{
    margin-top: 50px;
    &:first-child{
      margin-top: 0;
    }
  }
}

.alphabet-items{
  column-count: 3;
  column-gap: 20px;
  &._show-all{
    .letter-group{
      display: grid;
    }
  }
}

.letter-group{
  @include lastM(0 0 10px);
  display: none;
  width: 100%;
  grid-template-columns: 41px calc(100% - 41px);
  &._active{
    display: grid !important;
  }
  &__title{
    font-size: 22px;
    line-height: 118%;
    font-weight: 500;
  }
  &__items{
    @include grid100gap(10px);
  }
  &__item{
    font-size: 16px;
    line-height: 160%;
    a{
      &:hover{
        color: $primary;
        text-decoration: underline !important;
      }
    }
  }
}

.heal-types{
  padding: 25px 0;
  @media screen and (min-width: 993px){
    .tabs{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - 15px));
      gap: 30px;
    }
    .tab{
      padding: 0 !important;
      margin: 0 !important;
      height: 76px;
      @include center;
      font-weight: 700;
      font-size: 22px;
      border-radius: 10px;
      border: 1px solid $primary;
      color: $primary;
      &--active{
        background-color: $primary;
        color: #fff;
      }
      &:before{
        display: none;
      }
    }
  }
}

.letters{
  margin-top: 30px;
  display: flex;
  flex-wrap: wrap;
  &._selected{
    .letters__item:not(._active){
      color: #B9B9B9;
      border-color: #B9B9B9;
    }
  }
  &__item{
    margin-top: 20px;
    padding: 0 12px;
    display: flex;
    align-items: center;
    font-size: 18px;
    height: 21px;
    border-right: 1px solid $black;
    transition: all .3s;
    &:first-child{
      padding-left: 0;
    }
    &:last-child{
      border-right: none;
    }
    &:hover{
      cursor: pointer;
      color: $primary;
    }
  }
}

.article{
  padding: 25px 0;
  &__date{
    margin-bottom: 50px;
    text-align: right;
    color: #5C5C5C;
  }
}

.article-top{
  display: grid;
  grid-template-columns: calc(100% - 285px) 270px;
  gap: 15px;
  margin-bottom: 25px;
}

.article-nav{
  border-radius: 10px;
  background: #ECF9FF;
  padding: 20px 15px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &__link{
    font-size: 18px;
    line-height: 160%;
    @include lastM(0 0 25px);
    &:hover{
      color: $primary;
    }
  }
}

.guide-block{
  padding: 25px 0;
  &:last-child{
    padding-top: 40px;
  }
  .text{
    margin-top: 50px;
  }
}

.doctor-card{
  border-radius: 10px;
  overflow: hidden;
  &__top{
    height: unset;
  }
  &__img{
    @include imgCover(128%);
  }
}

.opinion{
  padding: 50px 0;
  background: #ECF9FF;
  margin: 25px 0;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 300px) 270px;
    gap: 30px;
    margin-top: 50px;
  }
}

.doctor-hint{
  margin: 25px 0;
  padding: 15px 0;
  background: #ECF9FF;
  text-align: center;
  &__inner{
    font-size: 18px;
    color: #5C5C5C;
    line-height: 119%;
    span{
      font-weight: 500;
      color: $primary;
    }
  }
}

.mob-tabs2{
  display: none;
}


@media screen and (max-width: 1200px){
  .letters{
    display: grid;
    grid-template-columns: repeat(23,1fr);
    margin-top: 50px;
    margin-left: -12px;
    width: calc(100% + 12px);
    gap: 20px 0;
    &__item{
      margin: 0;
      &:first-child{
        padding-left: 12px;
      }
      @media screen and (min-width: 993px){
        &:nth-child(1){
          //justify-content: flex-end;
        }
        &:nth-child(23n){
          border-right: none;
        }
      }
    }
  }
}


@media screen and (max-width: 992px){
  .services-info2{
    padding-bottom: 16px;
    &__grid{
      margin-top: 32px;
      grid-template-columns: 100%;
      gap: 32px;
    }
    &__img{
      order: -1;
    }
  }

  .heal{
    margin: 16px 0;
    padding: 32px 0;
    .alphabet-items{
      margin-top: 32px;
    }
  }

  .alphabet-items{
    column-count: 2;
  }

  .heal-types{
    padding: 16px 0;
    .mob-tabs{
      margin: 0;
    }
  }

  .letters{
    grid-template-columns: repeat(18,1fr);
    margin-top: 32px;
    &__item{
      justify-content: center;
      @media screen and (min-width: 769px){
        &:nth-child(18n){
          border-right: none;
        }
      }
    }
  }

  .guide-block{
    padding: 16px 0;
    &:last-child{
      padding-top: 16px;
    }
    .text{
      margin-top: 16px;
    }
  }

  .article{
    padding: 16px 0;
    &__inner{
      position: relative;
      padding-bottom: calc(55% + 46px);
    }
    &__date{
      position: absolute;
      left: 0;
      bottom: 0;
      text-align: left;
      margin: 0;
    }
  }

  .article-author{
    position: absolute;
    bottom: 46px;
    left: 0;
    padding-bottom: 52%;
    width: calc(100% / 3 - 10px);
    &__wrap{
      width: 100%;
      position: absolute;
    }
    .doctor-card{
      top: 0;
      left: 0;
      height: 100%;
      display: flex;
      flex-direction: column;
      &__top{
        flex-grow: 1;
      }
      &__img{
        height: 100%;
        img{
          object-fit: cover;
        }
      }
      &__bottom{
        padding: 25px;
        flex-grow: unset;
        flex-shrink: 0;
      }
      &__name,
      &__prof{
        font-size: 16px;
        line-height: 125%;
      }
      &__expirience{
        padding: 0 17px;
        height: 44px;
        font-weight: 700;
        font-size: 16px;
      }
    }
  }

  .article-top{
    grid-template-columns: 100%;
    gap: 0;
    margin-bottom: 32px;
  }

  .article-nav{
    padding: 0;
    background-color: transparent;
  }

  .opinion{
    margin: 16px 0;
    padding: 32px 0;
    &__grid{
      margin-top: 32px;
      grid-template-columns: 100%;
      gap: 32px;
    }
    &__doctor{
      width: calc(100% / 3 - 10px);
    }
  }

  .doctor-hint{
    margin: 16px 0;
    padding: 32px 0;
    text-align: left;
    &__inner{
      font-size: 16px;
      line-height: 160%;
    }
  }

  .mob-tabs2{
    display: block;
  }
}


@media screen and (max-width: 768px){
  .alphabet-items{
    column-count: 1;
  }

  .letters{
    grid-template-columns: repeat(15,1fr);
    &__item{
      @media screen and (min-width: 577px){
        &:nth-child(15n){
          border-right: none;
        }
      }
    }
  }

  .article{
    &__inner{
      padding-bottom: calc(80% + 46px);
    }
  }

  .article-author{
    width: calc(50% - 7.5px);
    padding-bottom: 76%;
  }

  .opinion{
    &__doctor{
      width: calc(50% - 7.5px);
    }
  }
}


@media screen and (max-width: 576px){
  .letters{
    grid-template-columns: repeat(10,1fr);
    &__item{
      @media screen and (min-width: 401px){
        &:nth-child(10n){
          border-right: none;
        }
      }
    }
  }

  .article{
    &__inner{
      padding-bottom: calc(135% + 46px);
    }
  }

  .article-author{
    width: 100%;
    padding-bottom: 128%;
    .doctor-card{
      &__img{
        padding-bottom: 105%;
      }
    }
  }

  .opinion{
    &__doctor{
      width: 100%;
      .doctor-card{
        &__img{
          padding-bottom: 105%;
        }
      }
    }
  }
}


@media screen and (max-width: 400px){
  .letters{
    grid-template-columns: repeat(9,1fr);
    &__item{
      @media screen and (min-width: 341px){
        &:nth-child(9n){
          border-right: none;
        }
      }
    }
  }

  .article{
    &__inner{
      padding-bottom: calc(145% + 46px);
    }
  }

  .article-author{
    padding-bottom: 135%;
  }
}


@media screen and (max-width: 340px){
  .letters{
    grid-template-columns: repeat(7,1fr);
    &__item{
      &:nth-child(7n){
        border-right: none;
      }
    }
  }
}
