.text34{
  font-size: 34px;
  line-height: 125%;
  color: $primary;
  font-weight: 700;
}

.text24{
  font-size: 1.5em;
  line-height: 119%;
}

.text16{
  font-size: 16px;
  line-height: 119%;
}

.bold-text{
  font-weight: 700;
}

.blue-text{
  color: $primary;
}

.text{
  h3{
    font-size: 24px;
    line-height: 119%;
    font-weight: 700;
    color: $primary;
    margin-bottom: 50px;
  }
  p{
    @include lastM(0 0 20px);
  }
  hr{
    margin: 0;
    opacity: 0;
    display: block;
    height: 30px;
  }
  img{
    width: 100%;
    display: block;
    border-radius: 10px;
  }
  ._view2{
    display: none;
  }
}


@media screen and (max-width: 992px) {
  .text34 {
    font-size: 22px;
    line-height: 118%;
  }

  .text22-tablet{
    font-size: 22px;
  }

  .text{
    h3{
      font-size: 22px;
      margin-bottom: 32px;
    }
    hr{
      height: 12px;
    }
    ._view1{
      display: none;
    }
    ._view2{
      display: block;
    }
  }
}
